"use client";

import { useRef } from "react";
import { motion, useInView } from "framer-motion";
import { ArrowRight, Chrome } from "lucide-react";
import Link from "next/link";
import Image from "next/image";

const stats = [
  { value: "10K+", label: "Active Users" },
  { value: "4.9", label: "Chrome Rating" },
  { value: "100%", label: "Free Forever" },
  { value: "<1min", label: "Setup Time" },
];

export function CTA() {
  const ref = useRef<HTMLDivElement>(null);
  const isInView = useInView(ref, { once: true, margin: "-100px" });

  return (
    <section id="get-started" className="section-padding bg-white relative">
      <div className="container-custom">
        <motion.div
          ref={ref}
          initial={{ opacity: 0, y: 20 }}
          animate={isInView ? { opacity: 1, y: 0 } : {}}
          transition={{ duration: 0.6 }}
          className="max-w-4xl mx-auto text-center"
        >
          {/* Logo */}
          <motion.div
            initial={{ scale: 0 }}
            animate={isInView ? { scale: 1 } : {}}
            transition={{ type: "spring", stiffness: 200, delay: 0.1 }}
            className="inline-block mb-8"
          >
            <div className="w-20 h-20 rounded-2xl overflow-hidden shadow-lg">
              <Image
                src="/simplest.png"
                alt="Simplest"
                width={80}
                height={80}
                className="w-full h-full object-cover"
              />
            </div>
          </motion.div>

          {/* Heading */}
          <h2 className="text-3xl md:text-4xl lg:text-5xl font-bold tracking-tight mb-4">
            Ready to automate?
          </h2>

          <p className="text-lg text-muted-foreground mb-8 max-w-xl mx-auto">
            Join thousands of users who have simplified their browser workflows.
            Free forever for personal use.
          </p>

          {/* CTA Button */}
          <div className="flex flex-col sm:flex-row items-center justify-center gap-4 mb-12">
            <Link
              href="https://chrome.google.com/webstore"
              target="_blank"
              className="btn-primary text-base px-8 py-4"
            >
              <Chrome className="w-5 h-5" />
              <span>Add to Chrome — Free</span>
              <ArrowRight className="w-4 h-4" />
            </Link>
          </div>

          {/* Stats */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            {stats.map((stat, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, y: 10 }}
                animate={isInView ? { opacity: 1, y: 0 } : {}}
                transition={{ duration: 0.4, delay: 0.3 + index * 0.1 }}
                className="p-4 rounded-xl bg-secondary/30 border border-border/50"
              >
                <p className="text-2xl font-bold text-gradient-coral">
                  {stat.value}
                </p>
                <p className="text-xs text-muted-foreground">{stat.label}</p>
              </motion.div>
            ))}
          </div>
        </motion.div>
      </div>
    </section>
  );
}
