"use client";

import { useRef } from "react";
import { motion, useInView } from "framer-motion";
import { Circle, Play, Share2, Clock, ArrowRight } from "lucide-react";
import { cn } from "@/lib/utils";

const steps = [
  {
    number: "01",
    icon: Circle,
    title: "Record",
    description:
      "Click Start and perform any actions. Every click, keystroke, and navigation is captured automatically.",
    color: "from-blue-500 to-cyan-500",
  },
  {
    number: "02",
    icon: Play,
    title: "Replay",
    description:
      "Run your automation with a single click. Watch it execute with a visual cursor showing each step.",
    color: "from-purple-500 to-pink-500",
  },
  {
    number: "03",
    icon: Share2,
    title: "Share",
    description:
      "Generate a shareable link instantly. Anyone can run your automation without installing anything.",
    color: "from-orange-500 to-red-500",
  },
  {
    number: "04",
    icon: Clock,
    title: "Schedule",
    description:
      "Set automations to run on a schedule. Perfect for reports, backups, and recurring tasks.",
    color: "from-green-500 to-emerald-500",
  },
];

export function HowItWorks() {
  const headerRef = useRef<HTMLDivElement>(null);
  const isHeaderInView = useInView(headerRef, { once: true, margin: "-100px" });

  return (
    <section id="how-it-works" className="section-padding bg-white relative">
      <div className="container-custom">
        {/* Header */}
        <motion.div
          ref={headerRef}
          initial={{ opacity: 0, y: 20 }}
          animate={isHeaderInView ? { opacity: 1, y: 0 } : {}}
          transition={{ duration: 0.6 }}
          className="text-center max-w-3xl mx-auto mb-16"
        >
          <h2 className="text-3xl md:text-4xl lg:text-5xl font-bold tracking-tight mb-4">
            Four steps to
            <br />
            <span className="text-gradient-coral">automation bliss</span>
          </h2>
          <p className="text-lg text-muted-foreground">
            No code required. No complex setup. Just record, replay, and let
            automation handle the rest.
          </p>
        </motion.div>

        {/* Steps */}
        <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
          {steps.map((step, index) => {
            const Icon = step.icon;
            return (
              <motion.div
                key={step.number}
                initial={{ opacity: 0, y: 20 }}
                animate={isHeaderInView ? { opacity: 1, y: 0 } : {}}
                transition={{ duration: 0.6, delay: index * 0.1 }}
                className="relative group"
              >
                {/* Connector line */}
                {index < steps.length - 1 && (
                  <div className="hidden lg:block absolute top-12 left-full w-full h-px">
                    <div className="w-full h-px bg-border" />
                    <ArrowRight className="absolute right-0 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground/30" />
                  </div>
                )}

                <div className="p-6 rounded-2xl border border-border bg-white hover:shadow-lg hover:border-border/80 transition-all duration-300">
                  {/* Step number */}
                  <span className="text-xs font-bold text-muted-foreground/40 mb-4 block">
                    {step.number}
                  </span>

                  {/* Icon */}
                  <div
                    className={cn(
                      "w-14 h-14 rounded-2xl flex items-center justify-center mb-4 bg-gradient-to-br shadow-sm transition-transform duration-300 group-hover:scale-110",
                      step.color
                    )}
                  >
                    <Icon className="w-7 h-7 text-white" />
                  </div>

                  {/* Content */}
                  <h3 className="text-xl font-semibold mb-2">{step.title}</h3>
                  <p className="text-sm text-muted-foreground leading-relaxed">
                    {step.description}
                  </p>
                </div>
              </motion.div>
            );
          })}
        </div>
      </div>
    </section>
  );
}
