function FeatureVisual({ feature }: { feature: (typeof tabs)[0] }) {
  if (feature.id === "recording") {
    return (
      <div className="card-elevated p-4 space-y-3">
        <div className="flex items-center gap-3 p-3 rounded-xl bg-red-50 border border-red-100">
          <div className="w-10 h-10 rounded-xl bg-red-500/10 flex items-center justify-center">
            <div className="w-3 h-3 rounded-full bg-red-500 animate-pulse" />
          </div>
          <div>
            <p className="text-sm font-medium">Recording...</p>
            <p className="text-xs text-muted-foreground">5 events captured</p>
          </div>
        </div>
        {["Click email input", "Type: user@example.com", "Click submit"].map(
          (step, i) => (
            <div
              key={i}
              className="flex items-center gap-2 px-3 py-2 rounded-lg bg-green-50 border border-green-100 text-xs"
            >
              <Check className="w-3.5 h-3.5 text-green-600" />
              <span className="text-green-800">{step}</span>
            </div>
          )
        )}
      </div>
    );
  }

  if (feature.id === "replay") {
    return (
      <div className="card-elevated p-6 text-center">
        <div className="w-16 h-16 rounded-2xl bg-primary/10 flex items-center justify-center mx-auto mb-4">
          <Play className="w-8 h-8 text-primary fill-current" />
        </div>
        <p className="font-medium mb-2">Running automation...</p>
        <div className="w-full h-2 bg-secondary rounded-full overflow-hidden">
          <motion.div
            initial={{ width: 0 }}
            animate={{ width: "75%" }}
            transition={{ duration: 2, repeat: Infinity }}
            className="h-full bg-primary rounded-full"
          />
        </div>
        <p className="text-xs text-muted-foreground mt-2">Step 3 of 4</p>
      </div>
    );
  }

  if (feature.id === "sharing") {
    return (
      <div className="card-elevated p-6">
        <div className="flex items-center gap-3 p-3 rounded-xl bg-green-50 border border-green-100 mb-4">
          <Check className="w-5 h-5 text-green-600" />
          <span className="text-sm font-medium text-green-800">
            Link copied to clipboard!
          </span>
        </div>
        <div className="px-4 py-3 rounded-xl bg-secondary border border-border font-mono text-xs break-all">
          simplest.dev/run/abc123xyz
        </div>
      </div>
    );
  }

  return (
    <div className="card-elevated p-6">
      <div className="flex items-center justify-between mb-4">
        <p className="text-sm font-medium">Schedule</p>
        <div className="px-2 py-1 rounded-full bg-green-100 text-green-700 text-xs font-medium">
          Active
        </div>
      </div>
      <div className="space-y-2 text-sm">
        <div className="flex justify-between py-2 border-b border-border">
          <span className="text-muted-foreground">Frequency</span>
          <span className="font-medium">Every day</span>
        </div>
        <div className="flex justify-between py-2 border-b border-border">
          <span className="text-muted-foreground">Time</span>
          <span className="font-medium">9:00 AM</span>
        </div>
        <div className="flex justify-between py-2">
          <span className="text-muted-foreground">Next run</span>
          <span className="font-medium">Tomorrow</span>
        </div>
      </div>
    </div>
  );
}
